import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService }
 from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-final',
  templateUrl: './final.component.html',
  styleUrls: ['./final.component.css']
})
export class FinalComponent implements OnInit {
  id: String;
  potTotal: Number;
  gameNum: Number;
  amount: Number;
  adminKey: Number;

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onFinalSubmit(){
    const finalUpd = {
      potTotal: this.potTotal,
      gameNum: this.gameNum,
      amount: this.amount
    }

    const adminKey = this.adminKey

    if(!this.validateService.validateResult(adminKey)){
      this.flashMessage.show('Final could not be updated.', { cssClass: 'alert-danger', timeout: 10000 });
    }


      this.authService.updateFinal(finalUpd, this.id).subscribe(data => {
        this.flashMessage.show('Final updated.', { cssClass: 'alert-success', timeout: 10000 });
        this.router.navigate(['/']);
      });
    // }
  }

}



